	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import jiaoshikebiao from '@/views/jiaoshikebiao/list'
	import xuesheng from '@/views/xuesheng/list'
	import jiaozhigong from '@/views/jiaozhigong/list'
	import examquestion from '@/views/exam/examquestion/list'
	import xueshengkebiao from '@/views/xueshengkebiao/list'
	import yanjiuxiangmu from '@/views/yanjiuxiangmu/list'
	import huodongxinxi from '@/views/huodongxinxi/list'
	import feiyongxinxi from '@/views/feiyongxinxi/list'
	import exampaper from '@/views/exampaper/list'
	import xiangmucanyu from '@/views/xiangmucanyu/list'
	import users from '@/views/users/list'
	import kechengleixing from '@/views/kechengleixing/list'
	import tousujianyi from '@/views/tousujianyi/list'
	import chengjixinxi from '@/views/chengjixinxi/list'
	import huodongbaoming from '@/views/huodongbaoming/list'
	import canjiahuiyi from '@/views/canjiahuiyi/list'
	import xuankezhongxin from '@/views/xuankezhongxin/list'
	import xuexiziliao from '@/views/xuexiziliao/list'
	import config from '@/views/config/list'
	import xuankexinxi from '@/views/xuankexinxi/list'
	import xueshuhuiyi from '@/views/xueshuhuiyi/list'
	import exampaperlist from '@/views/exam/exampaperlist/list'
	import examination from '@/views/exam/exampaperlist/examination'
	import examrecord from '@/views/exam/examrecord/list'
	import examfailrecord from '@/views/exam/examfailrecord/list'
	import jiaozhigongRegister from '@/views/jiaozhigong/register'
	import jiaozhigongCenter from '@/views/jiaozhigong/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/jiaozhigongCenter',
			name: '教职工个人中心',
			component: jiaozhigongCenter
		}
		,{
			path: '/news',
			name: '校园公告',
			component: news
		}
		,{
			path: '/jiaoshikebiao',
			name: '教师课表',
			component: jiaoshikebiao
		}
		,{
			path: '/xuesheng',
			name: '学生',
			component: xuesheng
		}
		,{
			path: '/jiaozhigong',
			name: '教职工',
			component: jiaozhigong
		}
		,{
			path: '/examquestion',
			name: '试题管理',
			component: examquestion
		}
		,{
			path: '/xueshengkebiao',
			name: '学生课表',
			component: xueshengkebiao
		}
		,{
			path: '/yanjiuxiangmu',
			name: '研究项目',
			component: yanjiuxiangmu
		}
		,{
			path: '/huodongxinxi',
			name: '活动信息',
			component: huodongxinxi
		}
		,{
			path: '/feiyongxinxi',
			name: '费用信息',
			component: feiyongxinxi
		}
		,{
			path: '/exampaper',
			name: '试卷管理',
			component: exampaper
		}
		,{
			path: '/xiangmucanyu',
			name: '项目参与',
			component: xiangmucanyu
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		,{
			path: '/kechengleixing',
			name: '课程类型',
			component: kechengleixing
		}
		,{
			path: '/tousujianyi',
			name: '投诉建议',
			component: tousujianyi
		}
		,{
			path: '/chengjixinxi',
			name: '成绩信息',
			component: chengjixinxi
		}
		,{
			path: '/huodongbaoming',
			name: '活动报名',
			component: huodongbaoming
		}
		,{
			path: '/canjiahuiyi',
			name: '参加会议',
			component: canjiahuiyi
		}
		,{
			path: '/xuankezhongxin',
			name: '选课中心',
			component: xuankezhongxin
		}
		,{
			path: '/xuexiziliao',
			name: '学习资料',
			component: xuexiziliao
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/xuankexinxi',
			name: '选课信息',
			component: xuankexinxi
		}
		,{
			path: '/xueshuhuiyi',
			name: '学术会议',
			component: xueshuhuiyi
		}
		, {
			path: '/exampaperlist',
			name: '考试列表',
			component: exampaperlist
		}, {
			path: '/examrecord',
			name: '考试记录',
			component: examrecord
		}, {
			path: '/examfailrecord',
			name: '错题本',
			component: examfailrecord
		}
		]
	},
	{
		path: '/jiaozhigongRegister',
		name: '教职工注册',
		component: jiaozhigongRegister
	},
	{
		path: '/examination',
		name: '考试',
		component: examination
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
